<Language lang="en" />
<HeadHTML
	title="Harlan York and Associates – Immigration Attorneys"
	description="Harlan York and Associates are the best immigration lawyers for Green Cards, Deportation, Family Immigration, and Naturalization in New York, New Jersey, and surrounding areas."
	keywords="Immigration Law, USA Immigration, Lawer, Attourney, Greencard, Visa"
>
	<meta name="example" content="This is an extra injected tag" />
</HeadHTML>
<HeadFacebook />
<HeadTwitter />
<div class="page">
	<Hero
		image_url="/img/hero_homepage_01.jpg"
		img_srcset="/img/hero_homepage_01.jpg"
		avif_srcset="/img/hero_homepage_01.avif"
		webp_srcset="/img/hero_homepage_01.webp"
		title="Harlan York and Associates"
		parallax="false"
	>
		<h1>{the_title}</h1>
	</Hero>
	<CallToAction add_class="floating-calltoaction" />
	<Services />
	<Team />
	<Hero
		image_url="/img/hero_homepage_02.jpg"
		img_srcset="/img/hero_homepage_02.jpg"
		avif_srcset="/img/hero_homepage_02.avif"
		webp_srcset="/img/hero_homepage_02.webp"
		title="Working with Clients"
		parallax="false">&nbsp;</Hero
	>
	<Testimonials />
	<Hero
		image_url="/img/hero_homepage_03.jpg"
		img_srcset="/img/hero_homepage_03.jpg"
		avif_srcset="/img/hero_homepage_03.avif"
		webp_srcset="/img/hero_homepage_03.webp"
		title="Helping Clients"
		parallax="false">&nbsp;</Hero
	>
	<Blog />
	<Featured />
	<FeaturedVideo />
	<SocialMedia />
	<CallToAction />
	<Ratings />
	<CalendarCard />
	<CallingCard />
</div>

<script>
	import { onMount, onDestroy } from 'svelte';
	/* ## WP ## */
	import { wp } from '$components/wp.js';
	/* ## Components ## */
	import Language from '$components/Language.svelte';
	import HeadHTML from '$components/HeadHTML.svelte';
	import HeadFacebook from '$components/HeadFacebook.svelte';
	import HeadTwitter from '$components/HeadTwitter.svelte';
	import Hero from '$components/Hero.svelte';
	import CallToAction from '$components/CallToAction.svelte';
	import Services from '$components/Services.svelte';
	import Team from '$components/Team.svelte';
	import Testimonials from '$components/Testimonials.svelte';
	import Blog from '$components/Blog.svelte';
	import Featured from '$components/Featured.svelte';
	import FeaturedVideo from '$components/FeaturedVideo.svelte';
	import SocialMedia from '$components/SocialMedia.svelte';
	import Ratings from '$components/Ratings.svelte';
	import CalendarCard from '$components/CalendarCard.svelte';
	import CallingCard from '$components/CallingCard.svelte';
	/* ## Data Loading ## */
	let unsubscribe_posts = () => {};
	let the_title = 'Loading...';
	import { Q_GET_POSTS } from '$components/wp.graphql.js';
	/* ## Main ## */
	onMount(async () => {
		let getPosts = $wp.data.gql(Q_GET_POSTS);
		unsubscribe_posts = await getPosts.subscribe(async (fetched_data) => {
			let data = await fetched_data;
			the_title = data?.posts?.edges[0]?.node?.title;
		});
	});
	/* ## Exit ## */
	onDestroy(() => {
		unsubscribe_posts(); // important for garbage collection otherwise memory leak
	});
</script>

<style>.page{display:block}.page h1{color:#fff;color:var(--color-white,#fff)}</style>
